<template>
  <view class="list_item" @click="toDetail">
    <view class="left">
      <image class="image" :src="showImage" mode="aspectFill" />
    </view>
    <view class="right">
      <view class="row1 flex_between">
        <view class="title">{{ info.name }}</view>
        <view class="flex-shrink-0"><LevelIcon :level="info.level" /></view>
        <view class="star-box" v-if="showStar">
          <image
            v-for="item in 5 - info.star_level"
            :key="item"
            src="/static/images/star.png"
            mode="scaleToFill"
            class="star-image"
          />
          <image
            v-for="item in info.star_level"
            :key="item"
            src="/static/images/star-fill.png"
            mode="scaleToFill"
            class="star-image"
          />
        </view>
        <view :class="['type-tag', getClass]" v-if="showRank">
          {{ info.rank_num }}
        </view>
      </view>
      <view class="row2">
        <paTag class="sport_item" v-for="(sport_item, sindex) in tagList.slice(0, 5)" :key="sindex">
          {{ sport_item }}
        </paTag>
        <paTag v-if="tagList.length > 5" class="sport_item">...</paTag>
      </view>
      <view class="row3">
        <view class="item">
          服务
          <text class="item-num">{{ info.total_service_num }}</text>
          次
        </view>
        <view class="item">
          累计时长
          <text class="item-num">{{ info.total_service_duration }}</text>
          小时
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import LevelIcon from '@/components/LevelIcon/index.vue'
export default {
  name: 'stadiumItem',
  components: {
    LevelIcon,
  },
  emits: ['reserve', 'click'],
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      },
    },
    type: {
      type: String,
      default: () => {
        return ''
      },
    },
    pageType: {
      type: String,
      default: 'level',
    },
  },
  data() {
    return {}
  },
  computed: {
    toDistance() {
      let item = this.info
      let distance = '位置未知'
      return distance
    },
    tagList() {
      return this.info.tag_ids_str.split(',')
    },
    showImage() {
      return this.info.avatar
        ? this.info.avatar
        : 'https://cdn-static.papa.com.cn/social/default-social.png'
    },
    getClass() {
      if (this.info.rank_num < 4) {
        return 'rank_' + this.info.rank_num
      } else {
        return 'rank_4'
      }
    },
    showRank() {
      return this.info.rank_num < 10 && this.info.rank_num > 0 && this.pageType == 'level'
    },
    showStar() {
      return this.pageType == 'star'
    },
  },
  methods: {
    getIcon(type, value) {
      return this.$dict.getDictLabel('promoteLevelList', value, {
        labelKey: type,
      })
    },
    handleReserve() {
      this.$emit('reserve', this.info)
    },
    toDetail() {
      uni.navigateTo({
        url: '/pages-sub/instructorDetail/index?instructor_id=' + this.info.instructor_id,
      })
    },
  },
}
</script>

